<template>
  <section class="hero is-light is-fullheight">
  <div class="hero-body">
    <div class="container">
      <div class="columns is-centered">
        <div class="column is-9">
          <span class="tag is-warning is-pulled-right">* Every whitespace is a pumpkin!!!</span>
<textarea @keyup="replaceToPumpkin" v-model="result" placeholder="Just type something..." class="textarea is-medium is-warning dark-textarea"  
       name="result" id="text"></textarea>
      
		<div id="result" :class="{ 'is-hidden': this.result.length==0 }">
      <div class="result">
        <img class="clipboard is-pulled-right" @click="copyToClipboard" src="https://cdn3.iconfinder.com/data/icons/basic-ui-31/16/07.Basic_UI_copy-paste-clipboard-file-512.png" alt="" id="clipboard">
         <p class="is-pulled-left"><strong>Result: </strong></p>
      </div>
      
      <pre>{{ replaced_text }}</pre>

      <b-input id="hidden_input" type="hidden" v-model="replaced_text"></b-input>
		</div>

    <p class="copyright">
      Sam Freeman - 2019&copy;</p>
        </div>
      </div>
			
  </div>
</div>
</section>
</template>

<script>

export default {
  name: 'app',

  data() {
    return {
      result: '',
      replaced_text: ''
    }
  },
  methods: {
    replaceToPumpkin() {
      this.replaced_text = this.result.replace(/ /g, ' :jack_o_lantert: ');
    },
    copyToClipboard() {
      let input = document.getElementById('hidden_input');
      input.setAttribute('type', 'text');
      input.select();
      document.execCommand('copy');
      input.setAttribute('type', 'hidden');
    }
  }
 
}
</script>

